.social-icons-animated .btn {
  opacity: 0;
  animation: slideInUp 0.6s ease-out forwards;
}

.social-icons-animated .btn:nth-child(1) {
  animation-delay: 1.1s;
}

.social-icons-animated .btn:nth-child(2) {
  animation-delay: 1.3s;
}

.social-icons-animated .btn:nth-child(3) {
