}

.social-icons-animated .btn:nth-child(4) {
  animation-delay: 1.7s;
}

.social-btn {
  position: relative;
  transition: all 0.3s ease;
}

.social-btn::after {
  content: attr(data-label);
  position: absolute;
  bottom: -35px;
  left: 50%;
  transform: translateX(-50%) scale(0);
  background: #333;
  color: #fff;
  padding: 6px 12px;
  border-radius: 6px;
  font-size: 0.85rem;
  white-space: nowrap;
  opacity: 0;
  transition: all 0.3s ease;
  pointer-events: none;
}

.social-btn::before {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%) scale(0);
  width: 0;
  height: 0;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-bottom: 6px solid #333;
  opacity: 0;
  transition: all 0.3s ease;
}

.social-btn:hover::after,
.social-btn:hover::before {
  opacity: 1;
  transform: translateX(-50%) scale(1);
}

.scroll-indicator {
  position: absolute;
  bottom: 30px;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: fit-content;
  animation: fadeIn 2s ease-out 2s both;
}

.scroll-indicator a {
  width: 40px;
  height: 40px;
  border: 2px solid #007bff;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  transition: all 0.3s ease;
  animation: bounce 2s ease-in-out infinite;
}

.scroll-indicator a:hover {
  background: #007bff;
  transform: translateY(-3px);
}

.scroll-indicator i {
  color: #007bff;
  font-size: 1.2rem;
  transition: color 0.3s ease;
}

.scroll-indicator a:hover i {
  color: #fff;
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(0.5);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes float {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

@keyframes slideInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

