  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
}

.proficiency-label {
  font-size: 1rem;
  font-weight: 600;
  color: #333;
}

.proficiency-percentage {
  font-size: 1rem;
  font-weight: 600;
  color: #007bff;
}

.proficiency-bar {
  width: 100%;
  height: 25px;
  background: #e9ecef;
  border-radius: 12px;
  overflow: hidden;
  position: relative;
}

.proficiency-fill {
  height: 100%;
  background: linear-gradient(90deg, #007bff 0%, #0056b3 100%);
  border-radius: 12px;
  transition: width 1.5s ease-in-out;
}

.proficiency-fill.animate {
  animation: fillBar 1.5s ease-in-out;
}

@keyframes fillBar {
  from {
    width: 0%;
  }
}

@keyframes slideFromLeft {
  from {
    opacity: 0;
    transform: translateX(-100px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideFromRight {
  from {
    opacity: 0;
    transform: translateX(100px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.animate-slide-left {
  opacity: 0;
  transition: opacity 0.6s ease, transform 0.6s ease;
}

.animate-slide-left.visible {
  animation: slideFromLeft 0.8s ease forwards;
}

.animate-slide-right {
  opacity: 0;
  transition: opacity 0.6s ease, transform 0.6s ease;
}

.animate-slide-right.visible {
  animation: slideFromRight 0.8s ease forwards;
}

/* Education Section */
.education-section {
  max-width: 900px;
  margin: 2rem auto 1rem;
  padding: 0 1rem;
}

.education-item {
  background: #fff;
  border-radius: 8px;
  padding: 0;
  margin-bottom: 0.5rem;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  overflow: hidden;
  display: flex;
  gap: 0;
  border-left: 4px solid #0056b3;
  position: relative;
}

.education-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  background: #007bff;
  transition: width 0.3s ease;
}

.education-item:hover::before {
  width: 6px;
}

.education-item:hover {
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.12);
}

.education-icon {
  background: #f8f9fa;
  min-width: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px 20px;
  border-right: 1px solid #e9ecef;
}

.education-icon i {
  font-size: 2.5rem;
  color: #0056b3;
  transition: transform 0.3s ease;
}

.education-item:hover .education-icon i {
  transform: scale(1.05);
}

.education-content {
  flex: 1;
  padding: 30px;
}

.education-degree {
  font-size: 1.4rem;
  font-weight: 600;
  color: #212529;
  margin-bottom: 6px;
  display: flex;
  align-items: center;
  gap: 8px;
  letter-spacing: -0.02em;
}

.education-degree i {
  font-size: 1.1rem;
  color: #0056b3;
}

.education-major {
  font-size: 1.1rem;
  color: #495057;
  font-weight: 500;
  margin-bottom: 12px;
}

.education-school {
  font-size: 1rem;
  color: #6c757d;
  margin-bottom: 8px;
  font-weight: 400;
  display: flex;
  align-items: center;
  gap: 6px;
}

.education-school i {
  color: #6c757d;
  font-size: 0.9rem;
}

.education-year {
  font-size: 0.95rem;
  color: #6c757d;
  margin-bottom: 12px;
  display: flex;
  align-items: center;
  gap: 6px;
  font-weight: 400;
}

.education-year i {
  color: #6c757d;
}

.education-highlights {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
  margin-top: 12px;
}

.highlight-badge {
  background: #e7f1ff;
  color: #0056b3;
  padding: 6px 14px;
  border-radius: 4px;
  font-size: 0.85rem;
  font-weight: 500;
  display: inline-flex;
  align-items: center;
  gap: 5px;
  border: 1px solid #cce5ff;
  transition: all 0.2s ease;
}

.education-item:hover .highlight-badge {
  background: #cce5ff;
  border-color: #99caff;
}

.highlight-badge i {
  font-size: 0.8rem;
}

/* Dark Mode Styles for Education Section */
[data-theme="dark"] .education-item {
  background: var(--bg-secondary);
  border-left-color: #4c9aff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}

[data-theme="dark"] .education-item:hover {
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.4);
}

[data-theme="dark"] .education-item::before {
  background: #4c9aff;
}

[data-theme="dark"] .education-icon {
  background: var(--bg-tertiary);
  border-right-color: var(--border-color);
}

[data-theme="dark"] .education-icon i {
  color: #4c9aff;
}

[data-theme="dark"] .education-degree {
  color: var(--text-primary);
}

[data-theme="dark"] .education-degree i {
  color: #4c9aff;
}

[data-theme="dark"] .education-major {
  color: var(--text-secondary);
}

[data-theme="dark"] .education-school {
  color: var(--text-tertiary);
}

[data-theme="dark"] .education-school i {
  color: var(--text-tertiary);
}

[data-theme="dark"] .education-year {
  color: var(--text-tertiary);
}

[data-theme="dark"] .education-year i {
  color: var(--text-tertiary);
}

[data-theme="dark"] .highlight-badge {
  background: rgba(76, 154, 255, 0.15);
  color: #4c9aff;
  border-color: rgba(76, 154, 255, 0.3);
}

[data-theme="dark"] .education-item:hover .highlight-badge {
  background: rgba(76, 154, 255, 0.25);
  border-color: rgba(76, 154, 255, 0.5);
}

/* Projects Section */
.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 25px; /* Increased gap */
}

.project-card {
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 25px; /* Increased padding */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.08);
  transition: transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out;
  display: flex;
  flex-direction: column;
}

.project-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.12);
}

.project-card h3 {
  color: #007bff;
  margin-top: 0;
  margin-bottom: 10px;
}

.project-card p {
  flex-grow: 1;
  margin-bottom: 15px;
}

.project-card img {
  max-width: 100%;
  height: auto;
  border-radius: 5px;
  margin-bottom: 15px;
}

.project-card a {
  display: inline-block;
  align-self: flex-start;
  margin-top: auto;
  background: #007bff;
  color: white;
  padding: 10px 18px;
  border-radius: 5px;
  text-decoration: none;
  transition: background-color 0.2s ease;
}

.project-card a:hover {
  background: #0056b3;
  text-decoration: none;
}

/* Hobbies Section */
.hobbies-section {
  padding: 80px 20px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  text-align: center;
  position: relative;
  overflow: hidden;
}

.hobbies-section::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(255,255,255,0.1) 1px, transparent 1px);
  background-size: 30px 30px;
  animation: backgroundMove 20s linear infinite;
}

@keyframes backgroundMove {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(30px, 30px);
  }
}

.hobbies-section h2 {
  font-size: 2.5rem;
  margin-bottom: 15px;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
  position: relative;
  z-index: 1;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
}

.hobbies-section h2 i {
  color: #ff6b6b;
  animation: heartbeat 1.5s ease-in-out infinite;
  filter: drop-shadow(0 0 10px rgba(255, 107, 107, 0.6));
}

@keyframes heartbeat {
  0%, 100% {
    transform: scale(1);
  }
  10%, 30% {
    transform: scale(1.2);
  }
  20%, 40% {
    transform: scale(1);
  }
}

.hobbies-intro {
  font-size: 1.1rem;
  color: rgba(255, 255, 255, 0.95);
  max-width: 600px;
  margin: 0 auto 30px;
  line-height: 1.6;
  position: relative;
  z-index: 1;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
}

.hobbies-container {
  position: relative;
  display: flex;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 60px;
  z-index: 1;
}

.scroll-arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 50px;
  height: 50px;
  background: rgba(255, 255, 255, 0.9);
  color: #667eea;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.3rem;
  z-index: 10;
  transition: all 0.3s ease;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(10px);
}

.scroll-arrow:hover {
  background: #fff;
  transform: translateY(-50%) scale(1.15);
  box-shadow: 0 6px 30px rgba(0, 0, 0, 0.3);
  color: #764ba2;
}

.scroll-arrow:active {
  transform: translateY(-50%) scale(1.05);
}

.scroll-arrow-left {
  left: 0;
}

.scroll-arrow-right {
  right: 0;
}

.hobbies-grid {
  display: flex;
  gap: 30px;
  width: 100%;
  padding: 30px 10px;
  overflow-x: auto;
  overflow-y: hidden;
  scroll-behavior: smooth;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.hobbies-grid::-webkit-scrollbar {
  display: none;
}

.hobby-card {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 0;
  padding: 35px 28px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.15);
  transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  position: relative;
  overflow: hidden;
  min-width: 260px;
  max-width: 280px;
  flex-shrink: 0;
  backdrop-filter: blur(10px);
  clip-path: polygon(10% 0%, 100% 0%, 90% 100%, 0% 100%);
  transform: skewY(-2deg);
}

.hobby-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 8px;
  height: 100%;
  background: linear-gradient(180deg, var(--hobby-color) 0%, transparent 100%);
  transform: scaleY(0);
  transform-origin: top;
  transition: transform 0.4s ease;
}

.hobby-card::after {
  content: '';
  position: absolute;
  bottom: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, var(--hobby-color) 0%, transparent 60%);
  opacity: 0;
  transform: scale(0);
  transition: all 0.5s ease;
}

.hobby-card:hover::before {
  transform: scaleY(1);
}

.hobby-card:hover::after {
  opacity: 0.12;
  transform: scale(1);
}

.hobby-card:hover {
  transform: skewY(0deg) translateY(-15px);
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.25);
  clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
}

.hobby-card > * {
  transform: skewY(2deg);
  transition: transform 0.4s ease;
}

.hobby-card:hover > * {
  transform: skewY(0deg);
}

.hobby-icon {
  width: 75px;
  height: 75px;
  margin: 0 auto 20px;
  background: linear-gradient(135deg, var(--hobby-color) 0%, var(--hobby-color) 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.4s ease;
  position: relative;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
  clip-path: polygon(30% 0%, 70% 0%, 100% 30%, 100% 70%, 70% 100%, 30% 100%, 0% 70%, 0% 30%);
}

.hobby-icon::before {
  content: '';
  position: absolute;
  inset: -5px;
  background: linear-gradient(135deg, var(--hobby-color), transparent);
  opacity: 0;
  transition: opacity 0.4s ease;
  z-index: -1;
  filter: blur(10px);
  clip-path: polygon(30% 0%, 70% 0%, 100% 30%, 100% 70%, 70% 100%, 30% 100%, 0% 70%, 0% 30%);
}

.hobby-card:hover .hobby-icon::before {
  opacity: 0.6;
}

.hobby-card:hover .hobby-icon {
  transform: rotate(45deg) scale(1.1);
  box-shadow: 0 12px 35px rgba(0, 0, 0, 0.25);
}

.hobby-icon i {
  font-size: 2.3rem;
  color: #fff;
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.2));
  transform: rotate(-45deg);
  transition: transform 0.4s ease;
}

.hobby-card:hover .hobby-icon i {
  transform: rotate(0deg);
}

.hobby-card h3 {
  font-size: 1.4rem;
  color: #2c3e50;
  margin-bottom: 10px;
  font-weight: 700;
  letter-spacing: 0.5px;
}

.hobby-card p {
  color: #666;
  font-size: 0.95rem;
  line-height: 1.6;
  font-weight: 400;
}

.contact-section {
  text-align: center;
  max-width: 100%;
  margin: 0 auto;
  padding: 60px 20px;
  background: #eef6ff;
}

.contact-section > * {
  max-width: 1100px;
  margin-left: auto;
  margin-right: auto;
}

.contact-header {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  margin-bottom: 1rem;
}

.contact-section h2 {
  font-size: 2.5rem;
  margin-bottom: 0;
  color: #0056b3;
}

.contact-logo {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  object-fit: cover;
  border: 3px solid #007bff;
  box-shadow: 0 4px 12px rgba(0, 123, 255, 0.3);
  transition: transform 0.3s ease;
}

.contact-logo:hover {
  transform: scale(1.1) rotate(5deg);
}

.contact-intro {
  font-size: 1.2rem;
  color: #666;
  max-width: 700px;
  margin: 0 auto 50px auto;
  line-height: 1.8;
}

.contact-cards-container {
  position: relative;
  display: flex;
  align-items: center;
  max-width: 1100px;
  margin: 40px auto 0;
  padding: 0 60px;
}

.contact-scroll-arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 45px;
  height: 45px;
  background: transparent;
  color: #007bff;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  z-index: 10;
  transition: all 0.3s ease;
}

.contact-scroll-arrow:hover {
  color: #0056b3;
  transform: translateY(-50%) scale(1.2);
}

.contact-scroll-arrow:active {
  transform: translateY(-50%) scale(1);
}

.contact-scroll-left {
  left: 0;
}

.contact-scroll-right {
  right: 0;
}

.contact-cards {
  display: flex;
  gap: 30px;
  overflow-x: auto;
  overflow-y: hidden;
  scroll-behavior: smooth;
  scrollbar-width: none;
  -ms-overflow-style: none;
  padding: 20px 5px;
  scroll-snap-type: x mandatory;
}

.contact-cards::-webkit-scrollbar {
  display: none;
}

.contact-card {
  background: #fff;
  padding: 30px 25px;
  border-radius: 12px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  text-decoration: none;
  color: #333;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  overflow: hidden;
  min-width: 260px;
  max-width: 280px;
  flex-shrink: 0;
  scroll-snap-align: center;
}

.contact-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 4px;
  background: linear-gradient(90deg, #007bff, #0056b3);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.contact-card:hover::before {
  transform: scaleX(1);
}

.contact-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 40px rgba(0, 123, 255, 0.2);
}

.contact-icon {
  width: 70px;
  height: 70px;
  background: linear-gradient(135deg, #007bff 0%, #0056b3 100%);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 18px;
  transition: transform 0.3s ease;
}

.contact-card:hover .contact-icon {
  transform: scale(1.1) rotate(5deg);
}

.contact-icon i {
  font-size: 1.8rem;
  color: #fff;
}

.contact-card h3 {
  font-size: 1.3rem;
  margin-bottom: 8px;
  color: #0056b3;
}

.contact-card p {
  font-size: 0.95rem;
  color: #666;
  margin-bottom: 18px;
  word-break: break-word;
}

.contact-action {
  font-size: 0.9rem;
  color: #007bff;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: gap 0.3s ease;
}

.contact-card:hover .contact-action {
  gap: 12px;
}

.contact-action i {
  font-size: 0.85rem;
  transition: transform 0.3s ease;
}

.contact-card:hover .contact-action i {
  transform: translateX(5px);
}

.contact-scroll-hint {
  text-align: center;
  color: #007bff;
  font-size: 0.85rem;
  font-weight: 600;
  margin-top: 15px;
  margin-bottom: 0;
  animation: hintPulse 2s ease-in-out infinite;
}

@keyframes hintPulse {
  0%, 100% {
    opacity: 0.6;
  }
  50% {
    opacity: 1;
  }
}

@media (min-width: 992px) {
  .contact-scroll-hint {
    display: none;
  }
}

.contact-navigation {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  margin-top: 60px;
  padding: 40px 20px 20px;
  border-top: 1px solid #e0e0e0;
}

.nav-signature {
  font-size: 1.3rem;
  font-weight: 700;
  color: #007bff;
  margin: 0;
  letter-spacing: 0.5px;
}

.nav-links {
  display: flex;
  gap: 5px;
  flex-wrap: wrap;
  justify-content: center;
}

.contact-navigation .nav-link {
  padding: 6px 16px;
  background: transparent;
  text-decoration: none;
  color: #666;
  font-size: 0.9rem;
  transition: color 0.3s ease;
  position: relative;
}

.contact-navigation .nav-link::after {
  content: '|';
  position: absolute;
  right: -3px;
  color: #ddd;
}

.contact-navigation .nav-link:last-child::after {
  content: '';
}

.contact-navigation .nav-link:hover {
  color: #007bff;
}

.app-footer {
  text-align: center;
  padding: 25px 20px;
  background: #fff;
  color: #333;
  margin-top: 30px;
  border-top: 1px solid #ddd;
}

/* Scroll to Top Button */
.scroll-to-top {
  position: fixed;
  bottom: 30px;
  right: 30px;
  width: 50px;
  height: 50px;
  background: #007bff;
  color: #fff;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  box-shadow: 0 4px 12px rgba(0, 123, 255, 0.4);
  z-index: 999;
  transition: all 0.3s ease;
  animation: fadeIn 0.3s ease;
}

.scroll-to-top:hover {
  background: #0056b3;
  transform: translateY(-5px);
  box-shadow: 0 6px 16px rgba(0, 123, 255, 0.6);
}

.scroll-to-top:active {
  transform: translateY(-2px);
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 991.98px) {
