  display: block;
  width: 100%;
}

.certificate-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 20px;
  margin-top: 2rem;
  padding: 0;
}

.certificate-card {
  background: #fff;
  border-radius: 12px;
  padding: 20px;
  box-shadow: 0 3px 15px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  text-decoration: none;
  color: #333;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  overflow: hidden;
  border: 2px solid transparent;
  max-width: 280px;
  width: 100%;
  margin: 0 auto;
  height: 260px;
}

.certificate-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 3px;
  background: linear-gradient(90deg, #ffc107 0%, #ff9800 100%);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.certificate-card:hover::before {
  transform: scaleX(1);
}

.certificate-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 12px 30px rgba(255, 193, 7, 0.25);
  border-color: #ffc107;
}

.certificate-badge {
  position: absolute;
  top: 12px;
  right: 12px;
  width: 28px;
  height: 28px;
  background: linear-gradient(135deg, #ffc107 0%, #ff9800 100%);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transform: scale(0) rotate(-180deg);
  transition: all 0.4s ease;
}

.certificate-card:hover .certificate-badge {
  opacity: 1;
  transform: scale(1) rotate(0deg);
}

.certificate-badge i {
  color: #fff;
  font-size: 0.85rem;
}

.certificate-image {
  width: 100px;
  height: 100px;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 15px;
  transition: transform 0.3s ease;
}

.certificate-card:hover .certificate-image {
  transform: scale(1.05);
}

.certificate-image img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  border-radius: 6px;
}

.certificate-details {
  text-align: center;
  width: 100%;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.certificate-details h3 {
  font-size: 0.95rem;
  color: #0056b3;
  margin-bottom: 8px;
  font-weight: 600;
  line-height: 1.3;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  text-overflow: ellipsis;
}

.certificate-issuer {
  font-size: 0.85rem;
  color: #666;
  margin-bottom: 12px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  flex-shrink: 0;
}

.certificate-issuer i {
  font-size: 0.75rem;
  color: #007bff;
}

.certificate-view {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  color: #007bff;
  font-weight: 600;
  font-size: 0.8rem;
  transition: gap 0.3s ease;
  width: 100%;
}

.certificate-card:hover .certificate-view {
  gap: 10px;
}

.certificate-view i {
  font-size: 0.7rem;
  transition: transform 0.3s ease;
}

.certificate-card:hover .certificate-view i {
  transform: translateX(3px);
}
