/* ============================================
   DARK MODE STYLES
   ============================================ */

/* Contact Section Dark Mode */
[data-theme="dark"] .contact-section {
  background: var(--bg-primary);
}

[data-theme="dark"] .contact-section h2 {
  color: #4c9aff;
}

[data-theme="dark"] .contact-intro {
  color: var(--text-secondary);
}

[data-theme="dark"] .contact-logo {
  border-color: #4c9aff;
  box-shadow: 0 4px 12px rgba(76, 154, 255, 0.3);
}

[data-theme="dark"] .contact-scroll-arrow {
  color: #4c9aff;
}

[data-theme="dark"] .contact-scroll-arrow:hover {
  color: #6eb5ff;
}

[data-theme="dark"] .contact-card {
  background: var(--bg-secondary);
  color: var(--text-primary);
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.4);
}

[data-theme="dark"] .contact-card:hover {
  box-shadow: 0 15px 40px rgba(76, 154, 255, 0.3);
}

[data-theme="dark"] .contact-card h3 {
  color: #4c9aff;
}

[data-theme="dark"] .contact-card p {
  color: var(--text-secondary);
}

[data-theme="dark"] .contact-action {
  color: #4c9aff;
}

/* Timeline/Experience Dark Mode */
[data-theme="dark"] .timeline-content {
  background: var(--bg-secondary);
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.4);
}

[data-theme="dark"] .timeline-content::before {
  border-right-color: var(--bg-secondary);
}

[data-theme="dark"] .timeline-content:hover {
  box-shadow: 0 8px 30px rgba(76, 154, 255, 0.2);
}

[data-theme="dark"] .timeline-content h3 {
  color: #4c9aff;
}

[data-theme="dark"] .timeline-company {
  color: #6eb5ff;
}

[data-theme="dark"] .timeline-content p {
  color: var(--text-secondary);
}

[data-theme="dark"] .timeline-period {
  color: var(--text-tertiary);
}

[data-theme="dark"] .experience-highlights li {
  color: var(--text-secondary);
}

[data-theme="dark"] .timeline-box {
  background: var(--bg-secondary);
}

/* Certificate Cards Dark Mode */
[data-theme="dark"] .certificate-card {
  background: var(--bg-secondary);
  color: var(--text-primary);
  box-shadow: 0 3px 15px rgba(0, 0, 0, 0.4);
  border-color: transparent;
}

[data-theme="dark"] .certificate-card:hover {
  box-shadow: 0 12px 30px rgba(255, 193, 7, 0.2);
  border-color: #ffc107;
}

[data-theme="dark"] .certificate-details h3 {
  color: var(--text-primary);
}

[data-theme="dark"] .certificate-issuer {
  color: var(--text-secondary);
}

[data-theme="dark"] .certificate-view {
  color: #ffc107;
}

[data-theme="dark"] .certifications-section .Heading h2 {
  color: #4c9aff;
}

[data-theme="dark"] .certifications-section {
  border-top-color: var(--border-color);
}
